/**
 * Options when opening an intent. If Bazaar is not installed, you can optionally
 * open the web fallback (cafebazaar.ir) or do nothing.
 */
export type OpenIntentOptions = {
  /**
   * If true and Bazaar is not installed, open the Cafe Bazaar web page in browser.
   * Default: false
   */
  openInBrowserIfNotInstalled?: boolean;
};
